body {
  font-family: 'Segoe UI';
  height: 1000px;
  margin-top: 40px;
}

.flex-wrapper {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  padding: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.spacer {
  margin-left: 40px;
}

.link {
  text-decoration: none;
  color: black;
}

.logo:hover {
  text-decoration: underline;
}

/*----------------TAB1------------------*/
.tab1,
.tab2,
.tab3 {
  padding: 0px 8px;
  display: inline;
  position: relative;
  float: left;
  cursor: pointer;
}

.tab1:hover,
.tab2:hover,
.tab3:hover {
  background-color: rgb(19, 131, 125);
}

.tab1-scroll {
  background-color: lightcoral;
  display: none;
  position: absolute;
  top: 21px;
  width: 130px;
  padding: 0px 0px;
}

.tab1:hover .tab1-scroll {
  display: block;
}

.tab1-scroll1:hover,
.tab1-scroll2:hover,
.tab1-scroll3:hover {
  background-color: rgb(180, 87, 87);
}

.tab1-scroll1,
.tab1-scroll2,
.tab1-scroll3 {
  padding: 5px 5px;
  position: relative;
}

.tab1-scroll2-sub {
  background-color: lightsteelblue;
  position: absolute;
  left: 130px;
  top: 0px;
  width: 140px;
  display: none;
}

.tab1-scroll2-sub1,
.tab1-scroll2-sub2, 
.tab1-scroll2-sub3 {
  padding: 5px;
}

.tab1-scroll2:hover .tab1-scroll2-sub {
  display: block;
}

.tab1-scroll2-sub1:hover,
.tab1-scroll2-sub2:hover, 
.tab1-scroll2-sub3:hover {
  background-color: rgb(127, 153, 187);
}

/*----------------TAB2------------------*/
.tab2-scroll {
  background-color: lightcoral;
  display: none;
  position: absolute;
  top: 21px;
  width: 130px;
  padding: 0px 0px;
}

.tab2:hover .tab2-scroll {
