}

.tab2-scroll1,
.tab2-scroll2,
.tab2-scroll3,
.tab2-scroll4 {
  padding: 5px 5px;
  position: relative;
}

.tab2-scroll1:hover,
.tab2-scroll2:hover,
.tab2-scroll3:hover,
.tab2-scroll4:hover {
